export default function SendForm(p) {
    const sendMessage = e => {
        e.preventDefault();
        if (e.target.message.value.trim() !== '') {
            p.sendMessage(e.target.message.value.trim());
            e.target.message.value = '';
        }
    }
    return (
        <form onSubmit={sendMessage}>
            <div className="flex x--between y--center">
                <div className="col">
                    <input placeholder="Enter message here" className="input" name="message" autoComplete='off' />
                </div>
                <div className="col--min">
                    <button className="btn">
                        <i className="fa fa-send"></i>
                    </button>
                </div>
            </div>
        </form>
    )
}