import React, { useState } from 'react';
import { Link, NavLink } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { logout } from 'store/user';
import { HOME, SHOP_PAGE, CART_PAGE, LOGIN_PAGE, ORDER_PAGE } from 'config';

export default function Header() {
    const [showNavbarOnMobile, setShowNavbarOnMobile] = useState(false);
    const user = useSelector(state => state.user);
    const cart = useSelector(state => state.cart);
    const dispatch = useDispatch();

    const toggleNavbarHandler = () => {
        setShowNavbarOnMobile(s => !s);
    }

    const logoutHandler = () => {
        dispatch(logout());
    }

    return (
        <header>
            <div className='container'>
                <nav className='navbar'>
                    <Link to={HOME + '/'} className='navbar-brand'>
                        <span>Boutique</span>
                    </Link>
                    <span className='navbar-toggle' onClick={toggleNavbarHandler}>
                        <i className='fa fa-bars'></i>
                    </span>
                    <div className={`navbar-menu ${showNavbarOnMobile ? 'active' : ''}`}>
                        <ul className="navbar-nav">
                            <li className="nav-item">
                                <NavLink className="nav-link" to={HOME + '/'}>Home</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" to={SHOP_PAGE}>Shop</NavLink>
                            </li>
                        </ul>
                        <ul className="navbar-nav">
                            <li className="nav-item">
                                <NavLink className="nav-link" to={CART_PAGE}>
                                    <i className='fa fa-shopping-cart'></i> Cart ({cart.items.length})
                                </NavLink>
                            </li>
                            {!user.token && (
                                <li className="nav-item">
                                    <NavLink className="nav-link" to={LOGIN_PAGE}>Login</NavLink>
                                </li>
                            )}
                            {user.token && (<>
                                <li className="nav-item">
                                    <NavLink className="nav-link" to={ORDER_PAGE}>Orders</NavLink>
                                </li>
                                <li className="nav-item">
                                    <span className='link' onClick={logoutHandler}>Logout</span>
                                </li>
                            </>)}
                        </ul>
                    </div>
                </nav>
            </div>
        </header>
    )
}