export default function MessageContent(p) {
    return (
        <>
            {p.messages.length > 0 && (
                p.messages.map((m, i) => {
                    return (
                        <div key={i}><div className={`message ${m.sender === 'request' ? 'message--out' : 'message--in'}`}>{m.content}</div></div>
                    )
                })
            )}
        </>
    )
}